import { SIGN_IN, SIGN_IN_SUCCESS, SIGN_IN_FAILED } from "../actionTypes/user";

const initialState = {
  user: undefined,
  error: false,
};

export default function userReducer(state = initialState, action = {}) {
  switch (action.type) {
    case SIGN_IN: {
      return { ...state, error: false };
    }
    case SIGN_IN_SUCCESS: {
      return { ...state, user: action.user };
    }
    case SIGN_IN_FAILED: {
      return {
        ...state,
        error: true,
      };
    }
    default: {
      return state;
    }
  }
}
